  width: 100%;
  height: 100%;
  max-width: none;
}

.arrows-default .swiper .swiper-button-next,
.arrows-default .swiper .swiper-button-prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 300;
  background: none;
  border: none;
  font-size: 0;
  cursor: pointer;
}
.arrows-default .swiper .swiper-button-next:after,
.arrows-default .swiper .swiper-button-prev:after {
